/*--------------------------------------------------------------
# Inner Circle Grid Links

1. Mobile
2 Desktop modifiers
--------------------------------------------------------------*/

@import "../node_modules/@asu-design-system/design-tokens/build/scss/_variables"; 
@import "media-queries";

@mixin like-an-h4 {
    font-size: $uds-size-font-large;
    letter-spacing: $uds-component-heading-four-letter-spacing;
    font-weight:700;
}

/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/

.ic-grid-links {
    grid-template-columns: 1fr;
    display: grid;
    grid-template-rows: auto;
    column-gap: $uds-size-spacing-3;
    row-gap: $uds-size-spacing-3;

    a {
        @include like-an-h4;
        border: 1px solid $uds-color-base-gray-3;
        color: $uds-color-base-gray-7;
        padding: $uds-size-spacing-3;
        text-decoration: none;

        svg {
            margin-right: $uds-size-spacing-1;
        }

        &:hover {
            text-decoration: underline;
        }
    }
}

/*--------------------------------------------------------------
2. Desktop modifiers
--------------------------------------------------------------*/

@mixin two-col {
    grid-template-columns: repeat( 2, 1fr);
}

@mixin three-col {
    grid-template-columns: repeat( 3, 1fr);
}

@mixin four-col {
    grid-template-columns: repeat( 4, 1fr);
}

@include media-breakpoint-up($md) {
    .ic-grid-links.mobile-at-md.two-columns {
        @include two-col;
    }
    .ic-grid-links.mobile-at-md.three-columns {
        @include three-col;
    }
    .ic-grid-links.mobile-at-md.four-columns {
        @include four-col;
    }
}

@include media-breakpoint-up($lg) {
    .ic-grid-links.mobile-at-lg.two-columns {
        @include two-col;
    }
    .ic-grid-links.mobile-at-lg.three-columns {
        @include three-col;
    }
    .ic-grid-links.mobile-at-lg.four-columns {
        @include four-col;
    }
}

@include media-breakpoint-up($xl) {
    .ic-grid-links.mobile-at-xl.two-columns {
        @include two-col;
    }
    .ic-grid-links.mobile-at-xl.three-columns {
        @include three-col;
    }
    .ic-grid-links.mobile-at-xl.four-columns {
        @include four-col;
    }
}