/*--------------------------------------------------------------
# Inner Circle Post Group Block

1. Mobile
2. Desktop, LG
3. Desktop, XL
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/
.ic-post-group {
  position: relative;
  padding-top: 1rem; }
  .ic-post-group:before {
    content: '';
    height: 1rem;
    width: calc(100% - 2rem);
    background-color: #ffc627;
    position: absolute;
    left: 1rem;
    top: 0; }
  .ic-post-group img {
    width: 100%;
    height: auto; }
  .ic-post-group a {
    padding: 0.5rem 1rem;
    display: block;
    text-decoration: none;
    color: #191919; }
    .ic-post-group a h4 {
      text-decoration: underline;
      color: #8c1d40; }
  .ic-post-group .story {
    padding: 0.5rem;
    border-bottom: 1px solid #d0d0d0; }
    .ic-post-group .story .event-meta svg {
      margin-right: 0.5rem;
      margin-left: 1rem; }
      .ic-post-group .story .event-meta svg:first-child {
        margin-left: 0; }

/*--------------------------------------------------------------
2. Desktop, LG
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
  .ic-post-group {
    display: grid;
    grid-template-columns: 20rem 1fr 1.5rem;
    grid-template-rows: 1.5rem 1fr 1.5rem;
    gap: 0px 0px; }
    .ic-post-group:before {
      display: none; }
    .ic-post-group img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      grid-row: 1 / span 3;
      grid-column: 2; }
    .ic-post-group .story-wrap {
      grid-row: 2;
      grid-column: 1; }
      .ic-post-group .story-wrap .story {
        transition: 0.5s cubic-bezier(0.19, 1, 0.19, 1);
        background-color: #fafafa;
        margin-bottom: 1rem;
        border: 1px solid #e8e8e8;
        border-right: 0;
        margin-left: 1.5rem; }
        .ic-post-group .story-wrap .story:hover {
          padding-right: 2rem;
          margin-left: 0rem; }
        .ic-post-group .story-wrap .story h4 {
          margin: 0 auto .25rem;
          color: #191919;
          text-decoration: none; }
        .ic-post-group .story-wrap .story .event-meta {
          margin-bottom: 0; }
        .ic-post-group .story-wrap .story a {
          color: #191919;
          text-decoration: none; }
        .ic-post-group .story-wrap .story.active {
          background-color: #ffc627;
          border-color: #ffc627;
          position: relative; }
          .ic-post-group .story-wrap .story.active:after {
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 16px 0 16px 27.7px;
            border-color: transparent transparent transparent #ffc627;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: -1.5rem; }
        .ic-post-group .story-wrap .story:last-child {
          margin-bottom: 0; }
        .ic-post-group .story-wrap .story:nth-child(3) {
          display: none; }
    .ic-post-group:after {
      content: '';
      width: 1.5rem;
      background-color: #ffc627;
      grid-column: 3;
      grid-row: 2; }
  .ic-post-group.reversed {
    grid-template-columns: 1.5rem 1fr 28rem; }
    .ic-post-group.reversed:after {
      grid-column: 1; }
    .ic-post-group.reversed img {
      grid-column: 2; }
    .ic-post-group.reversed .story-wrap {
      grid-column: 3; }
      .ic-post-group.reversed .story-wrap .story {
        margin: 0 1.5rem 1rem 0; }
        .ic-post-group.reversed .story-wrap .story:hover {
          margin-right: 0;
          padding-left: 1.5rem; }
        .ic-post-group.reversed .story-wrap .story.active {
          margin-right: 0;
          padding-left: 1.5rem; }
          .ic-post-group.reversed .story-wrap .story.active:after {
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 16px 27.7px 16px 0;
            border-color: transparent #ffc627 transparent transparent;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: -1.5rem; } }

/*--------------------------------------------------------------
3. Desktop, XL
--------------------------------------------------------------*/
@media (min-width: calc(1224px)) {
  .ic-post-group {
    grid-template-columns: 28rem 1fr 1.5rem; }
    .ic-post-group .story-wrap .story:nth-child(3) {
      display: block; }
  .ic-post-group.reversed {
    grid-template-columns: 1.5rem 1fr 28rem; } }

